import React, { Component } from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';

import NavbarView from './views/layout/NavbarView';
import CharacterDetails from './components/marvel/characters/CharacterDetails';
import Dashboard from './components/dashboard/Dashboard';
import CharacterGrid from './components/marvel/characters/CharacterGrid';
class App extends Component {

  render() {
    return (
      <BrowserRouter basename='/marvel-manager'>
        <div className="App">
          <NavbarView />
          <Switch>
            <Route exact path="/" component={Dashboard} />
            <Route exact path="/character" component={CharacterGrid} />
            <Route path="/character/:id" component={CharacterDetails} />
          </Switch>
        </div>
      </BrowserRouter>

    );
  }
}

export default App;
